import React, { Component } from "react"

export class CurrencyCal extends Component {
  constructor(props) {
    super(props)

    this.state = {
      two_k: 0,
      five_h: 0,
      two_h: 0,
      one_h: 0,
      fifty: 0,
      twenty: 0,
      total: 0,
      error: "",
    }
  }

  validate = (val) => {
    if (val.match(/^[a-zA-Z]+$/)) {
      const error = (
        <small style={{ color: "red" }}>Only numbers are allowed</small>
      )
      this.setState({ error })
    } else if (val < 0) {
      const error = (
        <small style={{ color: "red" }}>
          <strong>Count can't be negative</strong>
        </small>
      )
      this.setState({ error })
    } else {
      this.setState({ error: "" })
    }
  }

  on2kChange = (event) => {
    let val = event.target.value
    this.validate(val)
    if (val < 0) {
      val = 0
    }
    const total =
      val * 2000 +
      this.state.five_h * 500 +
      this.state.one_h * 100 +
      this.state.fifty * 50 +
      this.state.twenty * 20 +
      this.state.two_h * 200
    this.setState({ total, two_k: val })
  }

  on500Change = (event) => {
    let val = event.target.value
    this.validate(val)
    if (val < 0) {
      val = 0
    }
    const total =
      this.state.two_k * 2000 +
      val * 500 +
      this.state.one_h * 100 +
      this.state.fifty * 50 +
      this.state.twenty * 20 +
      this.state.two_h * 200
    this.setState({ total, five_h: val })
  }

  on200Change = (event) => {
    let val = event.target.value
    this.validate(val)
    if (val < 0) {
      val = 0
    }
    const total =
      this.state.two_k * 2000 +
      this.state.five_h * 500 +
      this.state.one_h * 100 +
      this.state.fifty * 50 +
      this.state.twenty * 20 +
      val * 200
    this.setState({ total, two_h: val })
  }

  on100Change = (event) => {
    let val = event.target.value
    this.validate(val)
    if (val < 0) {
      val = 0
    }
    const total =
      this.state.two_k * 2000 +
      this.state.five_h * 500 +
      val * 100 +
      this.state.fifty * 50 +
      this.state.twenty * 20 +
      this.state.two_h * 200
    this.setState({ total, one_h: val })
  }

  on50Change = (event) => {
    let val = event.target.value
    this.validate(val)
    if (val < 0) {
      val = 0
    }
    const total =
      this.state.two_k * 2000 +
      this.state.five_h * 500 +
      this.state.one_h * 100 +
      val * 50 +
      this.state.twenty * 20 +
      this.state.two_h * 200
    this.setState({ total, fifty: val })
  }

  on20Change = (event) => {
    let val = event.target.value
    this.validate(val)
    if (val < 0) {
      val = 0
    }
    const total =
      this.state.two_k * 2000 +
      this.state.five_h * 500 +
      this.state.one_h * 100 +
      this.state.fifty * 50 +
      val * 20 +
      this.state.two_h * 200
    this.setState({ total, twenty: val })
  }

  render() {
    const { error } = this.state
    return (
      <div>
        <br />
        <div
          className="container-sm border pt-3 shadow"
          style={{ maxWidth: "500px" }}
        >
          <h5
            className="card-header"
            style={{ textAlign: "center", backgroundColor: "white" }}
          >
            Calculate your currency
          </h5>
          <div className="card-body">
            <form>
              <label>{error}</label>

              <div className="form-group">
                <label>
                  <strong>2000 :</strong>
                </label>
                <input
                  type="number"
                  className="form-control"
                  value={this.state.two_k}
                  onChange={this.on2kChange}
                />
              </div>
              <div className="form-group">
                <label>
                  <strong>500 :</strong>
                </label>
                <input
                  className="form-control"
                  type="number"
                  value={this.state.five_h}
                  onChange={this.on500Change}
                />
              </div>
              <div className="form-group">
                <label>
                  <strong>200 :</strong>
                </label>
                <input
                  className="form-control"
                  type="number"
                  value={this.state.two_h}
                  onChange={this.on200Change}
                />
              </div>
              <div className="form-group">
                <label>
                  <strong>100 :</strong>
                </label>
                <input
                  className="form-control"
                  type="number"
                  value={this.state.one_h}
                  onChange={this.on100Change}
                />
              </div>
              <div className="form-group">
                <label>
                  <strong>50 :</strong>
                </label>
                <input
                  className="form-control"
                  type="number"
                  value={this.state.fifty}
                  onChange={this.on50Change}
                />
              </div>
              <div className="form-group">
                <label>
                  <strong>20 :</strong>
                </label>
                <input
                  className="form-control"
                  type="number"
                  value={this.state.twenty}
                  onChange={this.on20Change}
                />
              </div>
              <h4 style={{ color: "green" }}>Total: {this.state.total}</h4>
            </form>
          </div>
        </div>
      </div>
    )
  }
}

export default CurrencyCal
